<template>
  <div class="">
    <Header :back="true">添加</Header>
    <div class="content">
      <div class="container">
        <ul>
          <li>
            <label>书名</label>
            <input type="text" v-model="book.bookName">
          </li>
          <li>
            <label>信息</label>
            <input type="text" v-model="book.bookInfo">
          </li>
          <li>
            <label>价格</label>
            <input type="text" v-model="book.bookPrice">
          </li>
          <li>
            <label>封面</label>
            <input type="text" v-model="book.bookCover">
          </li>
        </ul>
        <button class="btn" @click="addBook">新增</button>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../components/Header.vue'
import { add } from '../model/add.js'
export default {
  name: 'Detail',
  data () {
    return {
      book: {
        bookName: '',
        bookInfo: '',
        bookPrice: '',
        bookCover: ''
      }
    }
  },
  methods: {
    async addBook () {
      let result = await add(this.book)
      if (result.code === 0) this.$router.push('/list')
    }
  },
  components: {
    Header
  }
}
</script>

